#display {
  min-height: 25vh;
  min-width: 450px;
  background-color: var(--purple-color);

  font-size: 2em;
  font-weight: bolder;
  color: var(--off-white-color);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

@media (max-width:641px) {
  #display {
    min-width: 100%;
    border-radius: 0;
  }
}


