#slider-flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 15px;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider-container {
  position: relative;
  height: 33px;
  width: 60px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1em;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

#slider-flex label {
  margin-bottom: 10px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color:  var(--purple-color);
}

input:focus + .slider {
  -webkit-box-shadow: 0 0 1px   var(--purple-color);
          box-shadow: 0 0 1px   var(--purple-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}


